/*.............CSS reset.........*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
margin: 0;
padding: 0;
scroll-behavior: smooth;
}

a {
text-decoration: none;
}

ul,
ol,
li {
list-style: none;
}

img {
vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
font-weight: inherit;
font-size: inherit;
}

html,
body {
height: 100%;
line-height: 1;
font-size: 100%;
color: #000;
-webkit-font-smoothing: antialiased;
}

.wrapper {
min-height: 100%;
overflow: hidden;
}