/*.............CSS reset.........*/
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-size: 100%;
  color: #000;
  -webkit-font-smoothing: antialiased;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background-colour);
  color: var(--text-colour-dark);
}

.container {
  width: min(100% - 40px, 1400px);
  margin-inline: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section {
  width: min(100% - 40px, 1400px);
  margin-inline: auto;
  text-align: center;
  padding-top: 4.5em;
}
.section .section__title {
  font-size: 2.3rem;
  font-weight: 500;
  margin-bottom: 0.5em;
}
.section .section__text {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.2rem;
  padding: 1em;
}

:root {
  --background-colour: #F3EDCF;
  --primary-colour: #4d3a7a;
  --accent-colour: #26FF7D;
  --accent-colour-dark: #00ed5f;
  --text-colour-dark: #111;
  --text-colour-light: #F3EDCF;
}

/*..........HEADER............*/
.header {
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(243, 237, 207, 0.6), rgba(243, 237, 207, 0.6)), url(/app/img/banner2.jpg);
  background-position: top;
  background-size: cover;
  position: relative;
}
.header__nav .nav__logo {
  font-size: 2rem;
  color: var(--primary-colour);
  font-weight: bold;
}
.header__nav ion-icon {
  display: none;
  width: 48px;
  height: 60px;
}
@media (max-width: 43.7485em) {
  .header__nav ion-icon {
    display: inline;
  }
}
.header__nav .nav__links {
  flex: 1;
  text-align: right;
  transition: 0.8s;
}
.header__nav .nav__links ion-icon {
  display: none;
  position: absolute;
  z-index: 3;
  top: 0.1em;
  right: 2em;
}
@media (max-width: 43.7485em) {
  .header__nav .nav__links ion-icon {
    display: block;
  }
}
@media (max-width: 43.7485em) {
  .header__nav .nav__links {
    position: absolute;
    background-color: var(--primary-colour);
    height: 90vh;
    width: 100%;
    top: -3000px;
    left: 0;
    text-align: right;
    z-index: 2;
    padding: 7em 2em 0.5em 0;
  }
}
.header__nav .nav__links li {
  display: inline-block;
  padding: 10px 11px;
  position: relative;
}
@media (max-width: 43.7485em) {
  .header__nav .nav__links li {
    display: block;
  }
}
.header__nav .nav__links a {
  color: var(--text-colour-dark);
  font-size: 1.3rem;
  transition: 0.8s;
}
@media (max-width: 43.7485em) {
  .header__nav .nav__links a {
    color: var(--background-colour);
  }
}
.header__nav .nav__links a:hover {
  color: var(--accent-colour);
}
.header .btn {
  display: inline-block;
  background-color: transparent;
  color: #fff;
  font-weight: 500;
  padding: 1.2em 3.5em;
  border-radius: 6px;
  font-size: 1rem;
  border: 1px solid var(--accent-colour);
  transition: 0.8ss;
}
.header .btn:hover {
  background-color: var(--accent-colour-dark);
}
.header__hero {
  max-width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.header__hero h1 {
  font-size: 2.8rem;
  font-weight: 500;
  margin-top: 3em;
}
@media (max-width: 43.7485em) {
  .header__hero h1 {
    font-size: 1.8rem;
  }
}
.header__hero p {
  font-size: 1.2rem;
  margin: 1em 0 2em;
}
@media (max-width: 43.7485em) {
  .header__hero p {
    font-size: 1rem;
  }
}
.header__hero .hero__btn {
  display: inline-block;
  background-color: var(--accent-colour);
  color: #fff;
  font-weight: 500;
  padding: 1.2em 3.5em;
  border-radius: 6px;
  font-size: 1.1rem;
  border: 0.8px solid var(--accent-colour-dark);
  position: relative;
}
@media (max-width: 43.7485em) {
  .header__hero .hero__btn {
    font-size: 0.9rem;
  }
}

/*..........HEADER end............*/
/*..........SERVICES............*/
.services__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 2.5em;
  flex-wrap: wrap;
}
.services__card {
  flex: 0 1 31%;
  background-color: var(--primary-colour);
  border-radius: 6px;
  padding: 1.5em;
  margin-bottom: 1em;
  box-sizing: border-box;
  line-height: 1.5em;
  transition: 0.8s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.services__card h2 {
  font-weight: 600;
  margin: 0.6em 0;
  text-transform: uppercase;
}
.services__card li > a {
  color: var(--text-colour-light);
}
@media (max-width: 43.7485em) {
  .services__card {
    flex: 0 1 49%;
  }
}
@media (max-width: 37.4987em) {
  .services__card {
    flex: 0 1 100%;
  }
}
.services__card:hover {
  box-shadow: 0 0 18px 0 hsla(144deg, 100%, 46%, 0.2);
}
.services__btn {
  display: inline-block;
  margin-top: 1.2em;
  background-color: transparent;
  color: var(--accent-colour);
  font-weight: 500;
  padding: 0.7em 2em;
  border-radius: 6px;
  font-size: 1.1rem;
  border: 2px solid var(--accent-colour);
  align-self: center;
}

/*..........SERVICES end............*/