


//mixins for flexbox

@mixin flexbox-sb {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin flexbox-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

//grid

@mixin grid-center {
  display: grid;
  place-items: center;
}


//center content xy

@mixin content-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}


